body {
  font-family: "Averta CY";
  font-style: normal;
}

.main {
  background-color: #ffffff;
  height: 100vh;
}

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 376px;
  background-image: url("../img/Background.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.header__logo {
  margin: 24px 0;
  width: 80px;
}

.header__content {
  font-family: "Averta CY";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  text-align: center;
  letter-spacing: -0.69px;
  font-feature-settings: "salt" on;
  color: #ffffff;
  width: 327px;
}

.block-form {
  display: flex;
  flex-direction: column;

  margin: 0 14px;
  margin-top: -208px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 48px 48px rgba(0, 18, 102, 0.08);
}

.block-form__title {
  margin-bottom: 2px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;

  font-feature-settings: "salt" on;

  color: #7a7e8f;
}

.block-form__price {
  margin-bottom: 16px;
}

.block-form__price-text {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -0.69px;
  font-feature-settings: "salt" on;

  color: #141a33;
}

.block-form__slidecontainer {
  position: relative;
  margin: 0 auto;
  z-index: 5;
}

input[type="range"] {
  cursor: pointer;
  width: 100%;
  -webkit-appearance: none;
  height: 8px;
  background: #eef0f3;
  border-radius: 5px;
  background-image: linear-gradient(#37c99e, #37c99e);
  background-repeat: no-repeat;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 24px;
  width: 36px;
  border-radius: 24px;
  background: url(../img/slider.svg) no-repeat center;
  background-color: #ffffff;
  cursor: grab;
  box-shadow: 0px 0px 4px rgba(81, 89, 133, 0.08),
    0px 12px 12px -4px rgba(81, 89, 133, 0.1);
  transition: background 0.3s ease-in-out;
}

input[type="range"]::-webkit-slider-thumb:active {
  cursor: grabbing;
}

input[type="range"]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  box-shadow: none;
  border: none;
  background: transparent;
}

.block-form__price-range {
  display: flex;
  justify-content: space-between;
  margin-bottom: 26px;
}

.price__range-item {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;

  font-feature-settings: "salt" on;
  color: #7a7e8f;
}

.block-form__button {
  cursor: pointer;
  padding: 0;
  width: 100%;
  max-width: 450px;
  margin: 0 auto;
  margin-bottom: 26px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;

  text-align: center;
  font-feature-settings: "salt" on;

  color: #ffffff;
  height: 60px;
  background: #37c99e;
  border-radius: 14px;
  border: 0;
}

.block-form__btnform {
  display: flex;
}

.block__desc-text {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  font-feature-settings: "salt" on;
  color: #7a7e8f;
}

.block-form__desc {
  display: flex;
  gap: 17px;
}

.block-form__slidecontainer {
  width: 100%;
  padding-bottom: 10px;
  z-index: 1;
}
